<template>
    <div>
        <b-container fluid>
            <b-row class="fcontainer">
                <b-col class="card" cols="9" v-for="quizItem in quizItems" :key="quizItem">
                    <h4 class="frage" v-bind="quizItem">{{ quizItem.frage }}</h4>
                    <b-row align-h="center">
                        <b-col cols="9">
                            <b-button block variant="light" v-for="antwort in quizItem.antworten" :key="antwort" @click="checkAnswer($event, antwort.right)">{{ antwort.text }}</b-button>
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
export default {
  props: {
    quizItems: {
      type: Array,
      default: () => []
    }
  },

  methods: {
    checkAnswer (event, right) {
      if (right === true) {
        event.target.style.backgroundColor = '#13e14e'
      } else {
        event.target.style.backgroundColor = '#f73030'
      }
    }
  }
}
</script>

<style lang="css" scoped>
    .default {
        background-color: #d8d8d8;
    }
    .default:hover {
        background-color: #f0f0f0;
    }
    .right {
        background-color: #f73030;
    }
    .card {
        background-color: #eeeeee;
        margin: 60px auto;
        padding: 60px 0;
        border: none;
        box-shadow: 0 16px 30px rgba(0, 0, 0, 0.091);
    }
